import React from 'react'
import { SelectProps } from 'antd/lib/select'
import useSwr from 'swr'
import { pathOr, head, forEach } from 'ramda'
import request from '@/utils/request'
import useSWR from '@/hooks/useSWR'
import Select from '@/components/select'
import { QueryVariables } from '@/constants/types'
const { compose } = require('ramda')

export type MedicalOrder = {
  CFINANCL: string
  CSECT: string
  HBED: string
  HBIRTHDT: string
  HHISNUM: string
  HNAMEC: string
  HNURSTA: string
  HSEX: string
  ORDATE: string
  ORDCLNM: string
  ORDOCNM: string
  ORHPCODE: string
  ORPROCED: string
  ORREQNO: string
  ORSPENAM: string
  ORSTATUS: string
  ORWORD: string
  ORWORKNO: string
  ZRSREGORD: string
}

export type Doctor = {
  id: number
  doctorId: number
  doctor: string
  division: 'PATH' | 'RAD'
}

export function usePathology() {
  const { data, ...others } = useSwr(`/api/b2/getPathologyResult`, url =>
    request(url, {
      method: 'POST',
      body: {
        classification: 'pathorderview',
        noType: 'HHISNUM',
        no: '16685033',
      },
    })
  )

  return {
    ...others,
    data: compose(head, pathOr([], []))(data) as MedicalOrder,
  }
}

export function useDoctors({
  division = 'PATH',
  ...variables
}: QueryVariables = {}) {
  const { data = [], ...rest } = useSWR<Doctor[]>(
    '/api/v1/c2/getC2DoctorList/{division}',
    {
      variables: { division, ...variables },
    }
  )

  const _data = data?.map(x => ({ ...x, id: x.doctorId }))

  const doctorById: { [key: string]: any } = {}

  forEach(s => (doctorById[s.doctorId] = s), _data)

  return {
    ...rest,
    doctorById,
    data: _data,
    doctorEnums: _data,
  }
}

export function DoctorSelect(props: SelectProps<any>) {
  const { data } = useDoctors()
  return (
    <Select {...props}>
      {data.map(x => (
        <Select.Option key={x.id} value={x.id}>
          {x.doctor}
        </Select.Option>
      ))}
    </Select>
  )
}
