import React from 'react'
import {
  atom,
  useRecoilState,
  atomFamily,
  selector,
  selectorFamily,
  useRecoilValue,
} from 'recoil'
import {
  find,
  mergeAll,
  map,
  values,
  head,
  pathOr,
  flatten,
  length,
  of,
  filter,
  keys,
} from 'ramda'
import Store from 'store'
import { userState } from '@/data/useUser'
import { setComponentByTab } from '@/components/snippet-template-list'
import { Snippets, SnippetProps, ReportTemplate } from '@/constants/types'
const { compose } = require('ramda')

// 
export const snippetCategoryOfReportState = selector({
  key: 'snippetCategoryOfReportState',
  get: ({ get }) => {
    const user = get(userState)

    if (user?.division === 'RAD') {
      return {
        PROCEDURE_INFORMATION: 'Procedure Information',
        CLINICAL_INFORMATION: 'Clinical Information',
        COMPARISON: 'Comparison',
        FINDINGS: 'Findings',
        IMPRESSION: 'Impression',
      }
    }

    return {
      PATHOLOGICAL_DIAGNOSIS: 'Pathological diagnosis',
      GROSS: 'Gross',
      MICROSCOPY: 'Microscopy',
      NOTES: 'Notes',
      REFERENCES: 'References',
    }
  },
})

const fakeReportTemplateList: ReportTemplate[] = setComponentByTab(
  Store.get('reportTemplateListState')
)

const reportTemplateListState = atom({
  key: 'reportTemplateListState',
  default: fakeReportTemplateList,
})

export const snippetsOfReportState = selectorFamily<
  ReportTemplate,
  React.Key | undefined
>({
  key: 'reportSnippetsState',
  get: id => ({ get }) => {
    return compose(find((x: ReportTemplate) => `${x.id}` === `${id}`))(
      setComponentByTab(get(reportTemplateListState))
    )
  },
})

const reportPreviewState = atom({
  key: 'reportPreviewState',
  default: false,
})

const categoryState = atomFamily<string, any>({
  key: 'categoryState',
  default: selector({
    key: 'categoryState/default',
    get: ({ get }) => compose(head, values)(get(snippetCategoryOfReportState)),
  }),
})

// 
export function useReportTemplateState() {
  return useRecoilState(reportTemplateListState)
}

// 
export function useReportTemplate({
  id,
  data,
}: {
  id?: React.Key
  data?: any
}) {
  const [reportTemplateList] = useRecoilState(reportTemplateListState)

  const snippetCategoryOfReport = useRecoilValue(snippetCategoryOfReportState)

  const defaultSnippetsOfReport = compose(
    mergeAll,
    map((key: string) => ({ [key]: [] })),
    values
  )(snippetCategoryOfReport)

  const [reportTemplate, setReportTemplate] = React.useState<ReportTemplate>({
    setting: defaultSnippetsOfReport,
  } as any)

  // const [reportTemplate] = useRecoilState<ReportTemplate>(
  //   snippetsOfReportState(id) as any
  // )

  React.useEffect(() => {
    if (data?.id) {
      setReportTemplate(compose(head, setComponentByTab, of)(data))
    }
  }, [data])

  React.useEffect(() => {
    if (id) {
      setReportTemplate(
        compose(find((x: ReportTemplate) => `${x.id}` === `${id}`))(
          setComponentByTab(reportTemplateList)
        )
      )
    }
  }, [id]) // eslint-disable-line

  let _snippetList = pathOr(defaultSnippetsOfReport, ['setting'])(
    reportTemplate
  )

  React.useEffect(() => {
    setSnippetList(_snippetList)
  }, [JSON.stringify(_snippetList)]) // eslint-disable-line

  const [snippetList, setSnippetList] = React.useState<Snippets>(_snippetList)

  let snippetById: { [key: string]: SnippetProps } = {}

  return {
    reportTemplate,
    snippetList,
    setSnippetList,
    categoryCountById: compose(
      mergeAll,
      map((key: string) => ({ [key]: length(snippetList[key]) })),
      keys
    )(snippetList),
    categoryCount: compose(length, keys)(snippetList),
    total: compose(
      length,
      filter((x: SnippetProps) => x.type !== 'TemplateSectionHeader'),
      flatten,
      values
    )(snippetList),
    snippetById: compose(
      map((x: SnippetProps) => (snippetById[x.id] = x)),
      flatten,
      values
    )(snippetList),
  }
}

// /
export function useReportPreview() {
  const [isPreview, setIsPreview] = useRecoilState(reportPreviewState)

  return [isPreview, () => setIsPreview(prev => !prev)] as [boolean, () => void]
}

// /
export function useToggleReportPreview(state: boolean = false) {
  const [isPreview, setIsPreview] = useRecoilState(reportPreviewState)

  React.useEffect(() => {
    setIsPreview(state)

    return () => {
      setIsPreview(false)
    }
  }, [state]) // eslint-disable-line

  return [isPreview, () => setIsPreview(prev => !prev)] as [boolean, () => void]
}

// /
export function useCurrentCategoryOfSnippet() {
  return useRecoilState<string>(categoryState(null) as any)
}

export function useSnippetCategoryOfReport() {
  return useRecoilValue(snippetCategoryOfReportState)
}
