import { confirm } from '@/utils/message'
import history from '@/utils/history'

export default function useRouteLeaveConfirm(pathname: string) {
  const handleConfirm = () => {
    confirm({
      title: '',
      content: '',
      okText: '',
      onOk: () => {
        if (pathname) {
          history.push(pathname)
        }
      },
    })
  }

  return handleConfirm
}
