import React from 'react'
import styled from 'styled-components'
import { length, keys, pathOr, gt, __, map } from 'ramda'
import cx from 'classnames'
import { Row, Col, Divider as AntDivider, Form } from 'antd'
import { useBreadcrumb } from '@/hooks/useApp'
import { useAuth } from '@/hooks/useAuth'
import { createFormItemLayout } from '@/utils/form'
import {
  Snippets,
  SnippetProps,
  ReportTemplateSettingOption,
} from '@/constants/types'
import { Input } from '@/components/input'
import { MedicalOrder } from '@/services/pathology'
import { mapIndexed } from '@/utils/webHelper'
const { compose } = require('ramda')

type Props = {
  className?: string
  style?: React.CSSProperties
  data: {
    template: any
    snippetList: Snippets
    medical: MedicalOrder
  }
}

const formItemLayout = createFormItemLayout(8)
const shortFormItemLayout = createFormItemLayout(3)

const FormItem = styled(Form.Item)`
  margin-bottom: 12px;
`

const Divider = styled(AntDivider)`
  margin: 16px 0;
`

const SnippetPreviewWrapper = styled.div`
  .ant-card-head {
    min-height: auto;
  }

  .ant-card-head-title {
    padding-top: 0;
    font-size: 14px;
  }

  .ant-card-body {
    padding-top: 6px;
    padding-bottom: 8px;
    padding-left: 18px;
  }
`

export default function ReportTypingPreview({ className, style, data }: Props) {
  useBreadcrumb([{ name: '' }, { name: '' }])

  const { snippetList, medical } = data

  const { user } = useAuth()

  return (
    <div style={style} className={cx('js-export-pdf', className)}>
      <Row className="mb-3">
        <Col span={24}>
          <div className="text-xl text-center mt-2">
            
            <span className="mx-6">
              {user.division === 'RAD' ? '' : ''}
            </span>
            
          </div>
        </Col>
      </Row>
      <Divider />
      <Row gutter={16}>
        <Col span={8}>
          <FormItem {...formItemLayout} label="">
            <Input readOnly bordered={false} value={medical?.HBED} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...formItemLayout} label="">
            <Input readOnly bordered={false} value={medical?.HNAMEC} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...formItemLayout} label="">
            <Input readOnly bordered={false} value={medical?.HHISNUM} />
          </FormItem>
        </Col>
      </Row>
      <Row gutter={16}>
        <Col span={8}>
          <FormItem {...formItemLayout} label="">
            <Input readOnly bordered={false} value={medical?.HSEX} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...formItemLayout} label="">
            <Input readOnly bordered={false} value={'xxxxx'} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...formItemLayout} label="">
            <Input readOnly bordered={false} value={medical?.HBIRTHDT} />
          </FormItem>
        </Col>
      </Row>
      <Row gutter={16}>
        <Col span={8}>
          <FormItem {...formItemLayout} label="">
            <Input readOnly bordered={false} value={medical?.ORREQNO} />
          </FormItem>
        </Col>
        <Col offset={8} span={8}>
          <FormItem {...formItemLayout} label="">
            <Input readOnly bordered={false} value={medical?.ORWORKNO} />
          </FormItem>
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <FormItem {...shortFormItemLayout} label="">
            <div
              dangerouslySetInnerHTML={{ __html: medical?.ORWORD }}
              style={{
                maxWidth: '100%',
                whiteSpace: 'initial',
                borderBottom: '1px solid #d9d9d9',
                paddingBottom: 5,
                paddingLeft: 5,
              }}
            />
          </FormItem>
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <FormItem {...shortFormItemLayout} label="">
            <Input readOnly bordered={false} value={'xxxx'} />
          </FormItem>
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <FormItem {...shortFormItemLayout} label="">
            <Input readOnly bordered={false} value={medical?.CSECT} />
          </FormItem>
        </Col>
      </Row>
      <Divider />
      <Row>
        <Col span={24}>
          <SnippetPreviewWrapper>
            {compose(
              map((key: string) => (
                <div
                  key={key}
                  css={{
                    fontSize: 15,
                    lineHeight: 1.65,
                    color: 'rgba(0, 0, 0, 0.85)',
                  }}>
                  {compose(
                    gt(__, 0),
                    length,
                    pathOr([], [key])
                  )(snippetList) && (
                    <h2 className="mb-1 font-normal" css={{ fontSize: 16 }}>
                      {key}
                    </h2>
                  )}
                  {compose(
                    mapIndexed(
                      ({ setting, ...x }: SnippetProps, idx: number) => (
                        <div key={x.id}>
                          {(() => {
                            if (x.inline) {
                              return (
                                <div
                                  dangerouslySetInnerHTML={{ __html: x.html }}
                                />
                              )
                            }

                            if (!setting) {
                              return ''
                            }

                            const content = (
                              <div>
                                {idx + 1}. {setting.text}
                                {setting.value ? '' + setting.value : ''}
                              </div>
                            )

                            if (setting.options && !setting.value) {
                              return (
                                <div>
                                  {content}
                                  {mapIndexed(
                                    (
                                      o: ReportTemplateSettingOption,
                                      oIdx: number
                                    ) => (
                                      <div key={o.id} className="pl-2">
                                        {'I'.padEnd(oIdx, 'I')}. {o.text}
                                      </div>
                                    ),
                                    setting.options
                                  )}
                                </div>
                              )
                            }

                            return content
                          })()}
                        </div>
                      )
                    ),
                    pathOr([], [key])
                  )(snippetList)}
                </div>
              )),
              keys
            )(snippetList)}
          </SnippetPreviewWrapper>
        </Col>
      </Row>
      <Divider />
      <Row gutter={16}>
        <Col span={8}>
          <FormItem {...formItemLayout} label="">
            <Input bordered={false} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...formItemLayout} label="">
            <Input bordered={false} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...formItemLayout} label="">
            <Input bordered={false} />
          </FormItem>
        </Col>
      </Row>
      <Row gutter={16}>
        <Col span={12}>
          <FormItem {...formItemLayout} label="">
            <Input bordered={false} />
          </FormItem>
        </Col>
        <Col span={12}>
          <FormItem {...formItemLayout} label="">
            <Input bordered={false} />
          </FormItem>
        </Col>
      </Row>
      <Divider />
      <Row className="mb-4">
        <Col span={24}>
          <div className="text-center">000001</div>
        </Col>
      </Row>
    </div>
  )
}
